import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"

export const query = graphql`
  query BlogPageQuery($id: String!) {
    mdx(id: { eq: $id }) {
      id
      body
      tableOfContents(maxDepth: 2)
      slug
      frontmatter {
        title
        date
        description
        group
      }
    }
    allMdx(sort: { fields: frontmatter___path, order: ASC }) {
      group(field: frontmatter___group) {
        edges {
          node {
            frontmatter {
              path
              title
              parentTitle
              pageTitle
              group
            }
            id
          }
        }
      }
    }
  }
`

const Page = ({ data }) => {
  const post = data.mdx

  // 左侧目录
  const thisPageGroup = data.mdx.frontmatter.group
  let sidebarData = {}
  data.allMdx.group.forEach(j => {
    const nodesGroup = j.edges[0].node.frontmatter.group

    if (thisPageGroup === nodesGroup) {
      sidebarData = j.edges
    }
  })

  // 右侧目录
  const catalogue = post.tableOfContents.items || []

  const seo = {
    title: post.frontmatter.title,
    description: post.frontmatter.description,
  }

  return (
    <Layout
      sidebarData={sidebarData}
      contentData={post}
      catalogue={catalogue}
      seo={seo}
    ></Layout>
  )
}

export default Page
